<template>
  <div class="hot-list">
    <div class="hotYear">
      <p>{{params.year}}年</p>
    </div>
    <div class="w-rank">
      <div class="rankList">
        <p>1</p>
        <p>2</p>
        <p>3</p>
      </div>
      <div class="chart">
        <div
          class="bar-item"
          v-for="(item, index) in params.data"
          :key="index"
        >
          <label :title="item.name">{{item.name}}</label>
          <div class="bar-cont">
            <div class="bar-img" :style="{width:barW(item.value)}"></div>
          </div>
          <div class="inc">{{item.value}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "hotList",
    props: {
      params: {
        type: Object
      }
    },
    data() {
      return {
        hotListData: [],
        barBgW: 0
      }
    },
    mounted() {
      for (let [index, item] of this.params.data.entries()) {
        this.barBgW = (this.barBgW > item.value ? this.barBgW : item.value)
      }
    },
    methods: {
      barW(value) {
        return (value / this.barBgW) * 100 + "%"
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .hot-list
    width 100%
    height 1.7rem
    display flex
    box-shadow 0 0 0.2rem rgba(0, 176, 255, 0.35) inset
    .hotYear
      font-size 0.24rem
      color #fff
      text-shadow 0 0 5px #00b0ff
      line-height 1.1rem
      width 1.85rem
      position relative
      text-align center
      padding 0.3rem 0
      box-sizing border-box
      p
        border-right 1px dashed rgba(71, 232, 243, 0.3)

        &:before
          content ''
          position absolute
          display block
          width 0.35rem
          height 0.35rem
          background rgba(0, 176, 255, 0.3)
          border-radius 50%
          right -0.17rem
          top 50%
          transform translate(0, -50%)

        &:after
          content ''
          position absolute
          display block
          width 0.19rem
          height 0.19rem
          background rgba(0, 176, 255, 1)
          border-radius 50%
          right -0.09rem
          top 50%
          transform translate(0, -50%)

    .w-rank
      flex 1
      display flex
      position relative

      .rankList
        width 1.2rem
        text-align right
        position absolute
        top 50%
        transform translate(0, -50%)

        p
          font-size 0.24rem
          color #fff
          line-height 0.45rem
          position relative

          &:first-child
            &:before
              content 'TOP'
              color #ffa800
              font-size 0.24rem
              position absolute
              right 0.24rem

      .chart
        flex 1
        padding-left 1.4rem
        padding-top 0.28rem

        .bar-item
          display flex
          height 0.28rem
          line-height 0.28rem
          margin-bottom 0.16rem

          label
            width 0.9rem
            margin-right 0.15rem
            color #fff
            font-size 0.18rem
            text-align right
            overflow hidden
            text-overflow ellipsis
            white-space nowrap

          .bar-cont
            width 2.8rem
            margin-right 0.15rem
            position relative

            .bar-img
              position absolute
              top 0
              bottom 0
              left 0
              background url('../../../assets/pictorialBg.png') no-repeat left
              background-size 2.8rem 100%
              width 0
              transition width 30s
          .inc
            flex 1
            font-size 0.18rem
            color #fff
</style>
