<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'信访量一级内容分布'"></ctitle>
        <div class="left-t">
          <div class="count">
            <allCount class="all-count" :count="allcount"></allCount>
          </div>
          <div class="detail">
            <detailList :params="detailList"></detailList>
          </div>
        </div>
        <div class="inner3">
          <div class="chart">
            <pie-chart3D :params="wtflPieParams"  @change="popShow"></pie-chart3D>
          </div>
        </div>
      </div>
      <div class="w-right">
        <ctitle :title="'信访量一级内容热点'"></ctitle>
        <div class="inner">
          <hotList
            v-if="item.data.length"
            v-for="(item, index) in hotListData"
            :key="index"
            :params="item"
          ></hotList>
        </div>
      </div>
    </div>
    <!--数据反查弹框-->
    <popLists v-if="popLists" :data="popLists" ref="popLists"></popLists>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import { gradientColor } from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
  import hotList from "../component/hotList";
  import popLists from "@/components/popLists/index";

  export default {
    name: "nrfl",
    props: {
      btnStatus: {
        type: String,
      }
    },
    components: {
      ctitle,
      PieChart3D,
      allCount,
      detailList,
      hotList,
      popLists
    },
    data() {
      return {
        popLists: null,
        // gd_id3: '信访日期',
        //一级内容分布
        wtflPieParams: {
          data: [],
          color: ["#6f69ff", "#59eaff", "#74d650", "#f0ff87", "#ff965d", "#5fffb6", "#61c6ff", "#dda2ea", "#745cc5", "#ffc343", "#fb548f", "#a7f68d", "#6994ff"],
          angle: 30,
          radius: "40%",
          labelRadius: 60,
          depth3D: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        detailList: {},
        // 初信重信联名信占比
        allcount: -1,
        // 内容热点
        hotListData: [],
        hotYearArr: [],
        hotListIndex: 0
      };
    },
    mounted() {
      this.getAPI(this.date);
    },
    methods: {
      // 数据反查弹框
      popShow(obj) {
        let id = obj.id;
        // let id = obj.name;
        if (id) {
          get_http("popLists", {
            'value_id': id,
            'name': obj.name,
          })
            .then(res => {
              this.popLists = res.data;
              this.$nextTick(() => {
                this.$refs.popLists.popListsShow = true;
              })
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      // 内容热点
      handleHotList() {
        var myDate = new Date();
        var nowYear = myDate.getFullYear();
        this.hotYearArr = [nowYear, nowYear - 1, nowYear - 2];
        this.getHotListAPI();
      },
      getHotListAPI() {
        get_http("qjywjkXfjjkNrflHotBar", {
          year: this.hotYearArr[this.hotListIndex],
        })
        .then(res => {
          if (this.hotListIndex < this.hotYearArr.length) {
            let obj = {
              year: this.hotYearArr[this.hotListIndex],
              data: res.data.data
            };
            this.hotListData.push(obj);
            this.hotListIndex++;
            this.getHotListAPI();
          }
        })
        .catch(err => {
          console.log(err);
        });
      },
      getAPI(date) {
        // 1.一级内容分布（总数）
        get_http("qjywjkNrflZbTotal", {
          day: `[${date.startDate},${date.endDate}]`,
          gd_id3: this.btnStatus
        })
        .then(res => {
          this.allcount = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
        // 2.一级内容分布(列表)
        get_http("qjywjkNrflZbType", {
          gd_id3: this.btnStatus,
          day: `[${date.startDate},${date.endDate}]`,
          limit: 3
        })
        .then(res => {
          this.detailList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 3.一级内容分布3d饼图
        get_http("qjywjkNrflWtflPie", {
          gd_id3: this.btnStatus,
          day: `[${date.startDate},${date.endDate}]`,
        })
        .then(res => {
          this.wtflPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        this.handleHotList();
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val);
      },
      btnStatus(val) {
        this.getAPI(this.date);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding 0.2rem .3rem
      box-sizing border-box
      .w-left
        width 9.5rem
        height 100%
        position relative
        font-size 0
        border-right 1px #91e3f7 dashed
        display flex
        flex-direction column
        .left-t
          width 100%
          margin-top 0.1rem
          flex 2
          display flex
          justify-content space-around
          align-items center
          .detail,.count
            flex 1
        .inner3
          flex 3
          .chart
            width 100%
            height 100%
      .w-right
        flex 1
        height 100%
        display flex
        flex-direction column
        .inner
          display flex
          flex 1
          flex-direction column
          justify-content space-around
          padding 0 0.5rem
</style>
